import React from 'react';
import './App.css';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import ListFuncionarioComponent from './Components/ListFuncionarioComponent';
import HeaderComponent from './Components/HeaderComponent';
import FooterComponent  from './Components/FooterComponent';
import CreateFuncionarioComponent from './Components/CreateFuncionarioComponent';
import ViewFuncionarioComponent from './Components/ViewFuncionarioComponent';


function App() {
  return (
    <div>
    <Router>
      <HeaderComponent/>
        <div className="container">
          <Switch>

            <Route path = "/" exact components= {ListFuncionarioComponent}></Route>
            <Route path = "/funcionarios" exact component = {ListFuncionarioComponent}></Route>
            <Route path = "/add-funcionarios/:id" component = {CreateFuncionarioComponent}></Route>
            <Route path = "/view-funcionarios/:id" component = {ViewFuncionarioComponent}></Route>

          </Switch>
         </div>
       <FooterComponent/>
     </Router>
 </div>
  );
}

export default App;
