// Criar a pagina para adicionar e alterar um funcionario
import React, {Component} from "react";
import FuncionarioService from '../Service/FuncionarioService';

class CreateFuncionarioComponent extends Component{
    constructor(props){
        super(props)
        this.state = {
            id: this.props.match.params.id,
            primeiroNome: '',
            ultimoNome:'',
            emailId:''
        }  

       this.changerPrimeiroNomeHeadler = this.changerPrimeiroNomeHeadler.bind(this);
       this.changerUltimoNomeHeadler = this.changerUltimoNomeHeadler.bind(this);
       this.saveOrUpdateFuncionario = this.saveOrUpdateFuncionario.bind(this);
       console.log('criado');

    }
    
    componentDidMount(){
        if(this.state.id === '_add'){
            return
        }else{
            FuncionarioService.getFuncionarioById(this.state.id).then((res) =>{
                let funcionario = res.data;
                this.setState ({primeiroNome: funcionario.primeiroNome, ultimoNome: funcionario.ultimoNome, emailId: funcionario.emailId})
            })
        }
    }

    saveOrUpdateFuncionario = (func) => {
        func.preventDefault();
        let funcionario = {primeiroNome: this.state.primeiroNome, ultimoNome: this.state.ultimoNome, emailId: this.emailId};
        console.log('funcionario =>' + JSON.stringfy(funcionario));
           if(this.state.id === '_add'){
            FuncionarioService.createFuncionario(funcionario).then(res => {
                this.props.history.push('/funcionarios');
            });
           }else{
            FuncionarioService.updateFuncionario(funcionario, this.state.id).then(res =>{
                this.props.history.push('/funcionarios');
            });
           }
        }

      changePrimeiroNomeHeadler= (event) => {
        this.setState({primeiroNome: event.target.value});
      }

      changeUltimoNomeHeadler= (event) =>{
        this.setState({ultimoNome: event.target.value});
      }

      changeEmailHeadler= (event) =>{
        this.setState({emailId: event.target.value});
      }

      cancel(){
        this.props.history.push('/funcionarios');
      }
     
      getTitle(){
        if(this.props.state.id === '_add'){
            return<h3 className="text-center"> Inserir Funcionario</h3>
        }else{
            return<h3 className="text-center"> Alterar Funcionario</h3>
        }
      }

      render(){
        return(
            <div>
                <br></br>
                <div className="container">
                    <div className="row">
                        <div className="card col-md-6 offset-md-3">
                            {
                                this.getTitle()
                            }
                            <div className="card-body">
                                <form>
                                    <div className="form-group">
                                        <label>Primeiro Nome: </label>
                                        <input placeholder="Primeiro Nome" name="primeironome" className="form-control" value = {this.state.primeiroNome} onChange={this.changePrimeiroNomeHeadler}/>

                                    </div>
                                    <div className="form-group">
                                        <label> Ultimo Nome: </label>
                                        <input placeholder="Ultimo Nome" name="ultimonome" className="form-control" value = {this.state.ultimoNome} onChange={this.changeUltimoNomeHeadler}/>

                                    </div>

                                    <div className="form-group">
                                        <label> Email id: </label>
                                        <input placeholder="Endereço de Email" name="emailId" className="form-control" value = {this.state.emailId} onChange={this.changeEmailHeadler}/>

                                    </div>
                                    <button className="btn btn-sucess" onClick={this.saveOrUpdateFuncionario}> Salvar </button>
                                    <button className="btn btn-danger" onClick={this.cancel.bindthis()} style={{marginLeft: "10px"}}> Cancel </button>
                                </form>
                            </div>
                        </div>
                  </div>
             </div>
         </div>  
     )
   }
}
 export default CreateFuncionarioComponent
