// Codigo para visualizar os funcionarios
import React,{Components} from "react";
import FuncionarioService from "../Service/FuncionarioService";

class ViewFuncionarioComponent extends Components{
     constructor(props){
        super(props)
        this.state = {
            funcionario:{}
        }
     }

    componentDidMount(){
        FuncionarioService.getFuncionarioById(this.state.id).then(res => {
          this.setState({funcionario: res.data});
          
            
        })
    }

     render(){
        return(
            <div>
                <br></br>
                <div className="card col-md-6 offset-md-3">
                    <h3 className="text-center"> Dados dos Funcioanrios </h3>
                    <div className="card-body">
                        <div className="row">
                            <label> Primeiro Nome do Funcionario: </label>
                            <div> {this.state.funcionario.primeiroNome} </div> 
                        </div> 
                        <div className="row">
                            <label> Ultimo Nome do Funcionario: </label>
                            <div> {this.state.funcionario.ultimoNome} </div> 
                        </div> 
                        <div className="row">
                            <label> Email do Funcionario: </label>
                            <div> {this.state.funcionario.emailId} </div> 
                        </div> 
                    </div>

                </div>
            </div>
        )
     }
}

export default ViewFuncionarioComponent