// Componentes para Listar os funcionarios
import React, {component} from 'react';
import FuncionarioService from '../Service/FuncionarioService';


class ListFuncionarioComponent extends component {
      constructor(props){
        super(props)
        this.state={
             funcionarios:[]
        }
    this.addFuncionario = this.addFuncionario.bind(this);
    this.editFuncionario = this.editFuncionario.bind(this);
    this.deleteFuncionario = this.deleteFuncionario.bind(this);

    
    }

    deleteFuncionario(id){
        FuncionarioService.deleteFuncionario(id).then(res => {
            this.state({funcionarios: this.state.funcionarios.filter(funcionario =>funcionario.id !==id )});

        });
    }

    ViewFuncionario(id){
        // eslint-disable-next-line
        this.props.history.push('/view-funcionario/${id}');
    }

    editFuncionario(id){
        // eslint-disable-next-line
        this.props.history.push('/add-funcionario/${id}'); 
    }

    ComponetDidMount(){
        FuncionarioService.getFuncionarios().then((res) =>{

        });
    }

    addFuncionario(){
        this.props.history.push('/add-funcionario/_add'); 
    }
      render(){ 
        return(
            <div>
                <h2 className='text-center'> Lista de Funcionarios </h2>
                  <div className='row'>
                  <button className='btn btn-primary' onClick={this.addFuncionario} >Adicione funcionario </button>
            </div>
            <br></br>
            <div className='row'>
                <table className='table table-striped table-bordered'>
                    <thead>
                        <tr>
                            <th> Primerio Nome de Funcionario</th>
                            <th> Segundo Nome do Funcionario</th>
                            <th> Email do Funcionario</th>
                            <th> Eviar </th>
                        </tr>
                    </thead>
         <tbody> 
           {
            this.state.funcionarios.map(
                funcionario =>
                <tr Key = {funcionario.id}>
                    <td>{funcionario.primeiroNome}</td>
                    <td>{funcionario.ultimoNome}</td>
                    <td>{funcionario.emailId}</td>
                    <td>
                        <button onClick={()=> this.editFuncionario(funcionario.id)} className="btn btn-info">Alterar</button>
                        <button style={{marginLeft:"10px"}} onClick= {() => this.deleteFuncionario(funcionario.id)} className="btn btn-danger">Apagar</button>
                        <button style={{marginLeft:"10px"}} onClick= {() => this.viewFuncionario(funcionario.id)} className="btn btn-info">Visualizar</button>
                    </td>
                </tr>
             )
        }
     </tbody> 
    </table>
   </div>
 </div> 
        )
    }
}

export default ListFuncionarioComponent